.row_box.footer_boxes {
    grid-template-columns: repeat(5, 1fr);
    padding-bottom: 30px;
}

.footer_inner_box ul li {
    color: #000;
    font-family: "poppins";
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 36px;
    text-transform: capitalize;
}

.footer_inner_box ul li:first-child {
    font-weight: bold;
    text-transform: uppercase;
}

.flex_box.footer_boxes {
    justify-content: space-between;
    align-items: start;
}

footer .input_box {
    width: 100%;
}

.footer_inner_box span {
    color: #000;
    font-family: "poppins";
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 25px;
    text-transform: lowercase;
}

footer .search_box {
    margin: 10px 0px;
}

.copy-right p {
    color: #000;
    text-align: center;
}

.copy-right {
    padding: 30px;
    border-top: 2px dashed;
}

footer p {
    color: #000;
}

.footer_inner_box ul li.under_line {
    color: #000;
    font-family: "poppins";
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-decoration-line: underline;
    text-transform: lowercase;
    padding-bottom: 14px;
}

.footer_inner_box ul li strong {
    color: #000;
    font-family: "poppins";
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    text-transform: lowercase;
}

.social_icons ul li {
    display: inline-block;
    border: 2px dotted;
    border-radius: 100%;
    margin: 3px;
    height: 50px;
    width: 50px;
    text-align: center;
    padding: 8px;
}

footer {
    border-top: 4px dotted #000;
    padding: 60px 20px 0px;
}

.flex_box.footer_boxes {
    padding-bottom: 50px;
}

@media(max-width:575px) {
    .copy-right {
        padding: 10px;
    }

    .copy-right p {
        padding: 0;
    }

    p {
        font-size: 16px !important;
    }

    .social_icons ul li {
        margin: 0px 7px;
        height: 40px;
        width: 40px;
    }

    footer {
        padding: 20px 10px 0;
    }

    .flex_box.footer_boxes {
        padding-bottom: 20px;
    }

    .footer_inner_box ul li {
        font-size: 16px;
        line-height: 32px;
    }

    .footer_inner_box span {
        font-size: 16px;
    }
}

@media(max-width:768px) {
    .flex_box.footer_boxes {
        display: block;
    }

    .footer_inner_box.mb_flex {
        display: flex;
        justify-content: space-between;
    }

    .col_20 {
        width: 100%;
    }

    .col_40.flex_box {
        width: 100%;
    }


    .footer_inner_box {
        margin: 10px 0px;
    }

    .blog_content {
        margin: 15px 0px 0px;
    }
}

.footer_inner_box.mb_flex .col_50 {
    width: 100%;
}
@media(min-width:1025px) and (max-width:1600px){
    .flex_box.footer_boxes {
        gap: 25px;
    }
    .blogs_inner .row_box {
        grid-column-gap: 0px;
        grid-row-gap: 0px;
    }
    .blog_content h5 {
        font-size: 19px;
    }
    .blog_content p {
        font-size: 15px;
    }
    .blog_content button.btn_purple {
        font-size: 18px;
        padding: 8px 35px;
        line-height: normal;
    }
}
.col_50.none-border ul li {
    font-weight: normal;
    text-transform: capitalize;
}
@media(min-width:769px) and (max-width:992px){
    .flex_box.footer_boxes {
        gap: 10px;
        flex-wrap: wrap;
    }
    .footer_inner_box ul li.under_line {
        line-break: anywhere;
    }
    .col_40.flex_box {
        gap: 17px;
    }
}